import React from 'react';
import "./ImageNavigationButton.css";
import previous from "./previous.png";
import next from "./next.png";

function ImageNavigationButton(props){
    return(
        <div className="button_body">
            <div className="button_container">
                <div className="button_container_inner">
                    {renderButton(previous, "previous", "button left", props.previous)}
                    {renderButton(next, "next", "button right", props.next)}
                </div>
            </div>
        </div>
    );
}

function renderButton(icon, alt, classes, onClick){
    return(
        <div className={classes} onClick={onClick}>
            <img className="button_image" src={icon} alt={alt}/>
        </div>
    );
}

export default ImageNavigationButton;